.navbar {
  box-sizing: border-box;
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 20px;
  position: absolute;
}
.navbar .title {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 25px;
  color: white;
  font-family: sans-serif;
  text-shadow: 0px 0px 20px #ffffff00;
  text-decoration: underline;
  text-underline-offset: 7px;
  transition: all 0.1s ease-in;
  animation: underlines 2s ease-in alternate;
}
.navbar .title:hover,
.navlinks a:hover {
  color: #ffffff;
  text-decoration: underline;
  text-underline-offset: 2px;
  text-shadow: 0px 0px 20px #ffffff;
}
@keyframes underlines {
  0% {
    transform: translateY(40px);
    text-underline-offset: 40px;
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  80% {
    transform: translateY(40px);
    text-underline-offset: 40px;
  }
  100% {
    transform: translateY(0px);
    text-underline-offset: 2px;
  }
}
.navlinks {
  display: flex;
  justify-content: center;
  place-items: center;
  gap: 25px;
  font-size: 25px;
  padding: 10px 20px;
  backdrop-filter: blur(10px);
  border-radius: 10px;
}
.navlinks a {
  display: flex;
  place-items: center;
  gap: 5px;
  color: #ffffff;
  text-shadow: 0px 0px 30px #ff0000;
  transition: all 0.1s ease-in;
  text-decoration: none;
  text-underline-offset: 7px;
}

.sideLinks {
  position: fixed;
  top: 100px;
  left: 20px;
  display: grid;
  gap: 20px;
  place-items: center;
}
.sideLinks a {
  text-align: center;
  width: 100px;
  text-decoration: none;
  padding: 10px 20px;
  font-size: 20px;
  color: white;
  border: 2px solid #ffffff;
  border-radius: 40px;
  transition: all 0.5s ease-in;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.sideLinks a:hover {
  color: #ff7979;
  border: 2px solid #ff7979;
  text-shadow: 0px 0px 20px #ff0000;
}
#arrow {
  transition: transform 0.5s ease-in-out;
  transform: translateX(0px) rotate(0deg) scale(1);
}
.sideLinks a:hover > #arrow {
  transform: translateX(50px) rotate(90deg) scale(1.5);
}
